@import url('https://fonts.googleapis.com/css2?family=Spectral:wght@700&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding-top: 4rem;
    color: #333;
    --type-principal: Helvetica, Arial, sans-serif;
    --type-secondary: 'Spectral', Georgia;
    font-family: var(--type-principal);
}

h1, h2, h3, h4, p {
    margin: 0;
}

a {
    text-decoration: none;
    color: #333;
}

ul, li {
    list-style: none;
    padding: 0;
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
}

button, input {
    display: block;
    font: var(--type-principal) 1rem;
    color: #333;
}

.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.AppBody {
    flex: 1;
}

.container {
    padding: 0 min(68px, 4.5vw);
    margin: 0 auto;
}

.mainContainer {
    margin-top: 2rem;
}

.title {
    text-transform: capitalize;
    font-family: var(--type-secondary);
    line-height: 1;
    font-size: 3rem;
    margin: 1rem 0;
    position: relative;
    z-index: 1;
}

.title::after {
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #fb1;
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: .2rem;
    z-index: -1;
}

.btn {
    font-size: 1rem;
    font-family: var(--type-princial);
    cursor: pointer;
    border: none;
    border-radius: .4rem;
    background-color: #fb1;
    color: #764701;
    min-width: 8rem;
    padding: .8rem 1.2rem;
    box-sizing: border-box;
    transition: .1s;
}

.btn:hover, .btn:focus {
    outline: none;
    box-shadow: 0 0 0 3px #fea, 0 0 0 4px #fb1;
}

.btn:disabled {
    opacity: .5;
    cursor: wait;
}

.animeLeft {
    opacity: 0;
    transform: translateX(-20px);
    animation: animeLeft .3s forwards;
}

@keyframes animeLeft {
    to {
        opacity: 1;
        transform: initial;
    }
}